<template>
  <div class="home">
    <v-text-field
      @click:append="putNewList"
      @keyup.enter="putNewList"
      v-model="newTodoitem"
      clearable
      outlined
      label="Nueva Tarea"
      append-icon="mdi-send"
      hide-details
      class="text-field"
    ></v-text-field>

    <v-list flat>
      <v-list-item-group>
        <v-list-item
          v-for="item in todoList"
          :key="item.id"
          @click="item.state = !item.state"
          :class="{ 'light-green lighten-4': item.state }"
        >
          <template>
            <v-list-item-content>
              <v-list-item-title
                :class="[
                  { 'text-decoration-line-through': item.state },
                  { 'my-list-class': item.state },
                ]"
                >{{ item.text }}</v-list-item-title
              >
            </v-list-item-content>
            <v-list-item-icon>
              <v-icon @click.stop="deleteTodoItem(item.id)">mdi-delete</v-icon>
            </v-list-item-icon>
          </template>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  data: () => ({
    newTodoitem: null,
  }),
  computed: {
    ...mapGetters(["todoList"]),
  },

  methods: {
    ...mapActions(["putNewListItem", "deleteListItem"]),

    putNewList() {
      if (this.newTodoitem !== null) {
        let payload = { id: Date.now(), text: this.newTodoitem, state: false };
        this.putNewListItem(payload);
        this.newTodoitem = null;
      }
    },
    deleteTodoItem(id) {
      this.deleteListItem(id);
    },
  },
  updated() {
    if (this.todoList.length == 0) {
      localStorage.removeItem("todoRegistred");
      localStorage.removeItem("todoData");
    } else {
      localStorage.setItem("todoRegistred", "true");
      localStorage.setItem("todoData", JSON.stringify(this.todoList));
    }
  },
};
</script>
<style lang="scss" >
.home {
  .text-field {
    margin: 15px auto !important;
  }
  .mdi-send:hover {
    color: var(--color-black) !important;
  }
  .mdi-delete {
    color: rgba(0, 0, 0, 0.822);
    &:hover {
      color: var(--color-danger) !important;
    }
  }
  .v-list-item {
    margin-top: 2px;
    border-bottom: 0.5px solid rgba(0, 0, 0, 0.089);
  }
  .my-list-class {
    color: rgba(0, 0, 0, 0.452) !important;
  }
}
</style>